/* eslint-disable react/prop-types */
import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Formik } from 'formik';
import * as Yup from 'yup';
import Model from '../../../components/UI/Model/Model';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import {
  addDishSaga,
  editDishSaga,
  resetErrorMsg,
} from '../../../store/actions';
import {
  SELECT_CATEGORY,
  SELECT_FOOD_TYPE,
  PRICE_REQUIRED,
  PRICE_NUMBER,
  NAME_REQUIRED,
} from '../../../constants/errorConstants';

// eslint-disable-next-line no-unused-vars
const AddMenuItemsModal = props => {
  const { tableFilter, isAdd, editData } = props;
  const [photo, setPhoto] = useState(null);
  const [isDisable, setIsDisable] = useState(false);
  const [photoURL, setPhotoURL] = useState(null);
  const [isEditValueUpdated, setIsEditValueUpdated] = useState(false);
  const [initialValues, setInitialValues] = useState({
    selectedCategory: '',
    selectedSubCategory: '',
    selectedFoodType: '1',
    price: '',
    discountPrice: '',
    name: '',
    description: '',
  });

  const dispatch = useDispatch();

  const {
    categories: { list: categoriesOptions },
    subCategories: { list: subCategoriesOptions },
    // eslint-disable-next-line no-unused-vars
    errorMsgAddDish,
  } = useSelector(state => state.dish);

  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
    dispatch(resetErrorMsg());
  };

  if (editData) {
    if (!isEditValueUpdated) {
      const categoryIndex = categoriesOptions.findIndex(
        value => value.name === editData.categoryName,
      );
      // const subCategoryIndex = subCategoriesOptions.findIndex(
      //   value => value.name === editData.categoryName,
      // );
      setPhotoURL(editData.thumbnail);
      setInitialValues({
        selectedCategory: categoriesOptions[categoryIndex]._id,
        selectedSubCategory: subCategoriesOptions[categoryIndex]._id,
        selectedFoodType: '1',
        price: editData.price,
        discountPrice: editData.discountPrice || '',
        name: editData.name,
        description: editData.description || '',
      });
      setIsEditValueUpdated(true);
    }
  }

  // const foodTypes = [
  //   {
  //     _id: '1',
  //     label: 'Veg',
  //   },
  //   {
  //     _id: '2',
  //     label: 'Non-Veg',
  //   },
  // ];

  const validationSchema = Yup.object({
    selectedCategory: Yup.string().required(SELECT_CATEGORY),
    selectedFoodType: Yup.string().required(SELECT_FOOD_TYPE),
    price: Yup.number().typeError(PRICE_NUMBER).required(PRICE_REQUIRED),
    name: Yup.string().trim().required(NAME_REQUIRED),
    description: Yup.string().trim(),
  });

  const getImage = evt => {
    const { files } = evt.target;
    setPhoto(files[0]);
    setPhotoURL(URL.createObjectURL(files[0]));
  };

  const submitBtnHandler = values => {
    setIsDisable(true);
    const reqData = {
      name: values.name,
      price: values.price,
      isVeg: values.selectedFoodType === '1',
      categoryId: values.selectedCategory,
      subCategoryId: values.selectedSubCategory,
      discountPrice: values.discountPrice,
      description: values.description,
      tableFilter,
      closeModel,
      setIsDisable,
    };

    if (photo) {
      reqData.photo = photo;
    }

    if (isAdd) {
      dispatch(addDishSaga(reqData));
    } else {
      reqData.id = editData._id;
      dispatch(editDishSaga(reqData));
    }
  };

  return (
    <Model
      closeModel={closeModel}
      isDisable={isDisable}
      headerTitle={isAdd ? 'Add New Item' : 'Edit Menu Item'}
    >
      <Formik
        enableReinitialize
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={(values, { setSubmitting }) => {
          submitBtnHandler(values);
          setSubmitting(false);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          setFieldTouched,
          setFieldValue,
          // isSubmitting,
        }) => (
          <form onSubmit={handleSubmit}>
            <div className="from-group text-left">
              <h4 className="required-field">Select Category</h4>
              <select
                className="form-control"
                name="selectedCategory"
                placeholder="Select category"
                value={values.selectedCategory}
                onChange={event => {
                  setFieldValue('selectedCategory', event.target.value);
                }}
                onBlur={() => setFieldTouched('selectedCategory', true)}
              >
                <option value="" disabled>
                  Select Category
                </option>
                {categoriesOptions.map(item => {
                  if (item.status === true) {
                    return (
                      <option key={item._id} value={item._id}>
                        {item.name}
                      </option>
                    );
                  }
                  return null;
                })}
              </select>
              <div className="error-message">
                {errors.selectedCategory &&
                  touched.selectedCategory &&
                  errors.selectedCategory}
              </div>
            </div>
            <div className="from-group text-left">
              <h4 className="required-field">Select Sub Category</h4>
              <select
                className="form-control"
                name="selectedSubCategory"
                placeholder="Select Sub Category"
                value={values.selectedSubCategory}
                onChange={event => {
                  setFieldValue('selectedSubCategory', event.target.value);
                }}
                onBlur={() => setFieldTouched('selectedSubCategory', true)}
              >
                <option value="" disabled>
                  Select Sub Category
                </option>
                {subCategoriesOptions.map(item => {
                  return (
                    <option key={item._id} value={item._id}>
                      {item.name}
                    </option>
                  );
                })}
              </select>
              <div className="error-message">
                {errors.selectedSubCategory &&
                  touched.selectedSubCategory &&
                  errors.selectedSubCategory}
              </div>
            </div>
            {/* <div className="from-group text-left">
              <h4 className="required-field">Select Food Type</h4>
              <select
                className="form-control"
                name="selectedFoodType"
                placeholder="Select Food Type"
                value={values.selectedFoodType}
                onChange={event => {
                  setFieldValue('selectedFoodType', event.target.value);
                }}
                onBlur={() => setFieldTouched('selectedFoodType', true)}
              >
                {foodTypes.map(item => {
                  return (
                    <option key={item._id} value={item._id}>
                      {item.label}
                    </option>
                  );
                })}
              </select>
              <div className="error-message">
                {errors.selectedFoodType &&
                  touched.selectedFoodType &&
                  errors.selectedFoodType}
              </div>
            </div> */}
            <div className="from-group text-left">
              <h4 className="required-field">Price</h4>
              <input
                type="text"
                className="form-control cal-input"
                placeholder=""
                name="price"
                value={values.price}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.price && touched.price && errors.price}
              </div>
            </div>
            <div className="from-group text-left">
              <h4>Discounted price</h4>
              <input
                type="text"
                className="form-control cal-input"
                placeholder=""
                name="discountPrice"
                value={values.discountPrice}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.discountPrice &&
                  touched.discountPrice &&
                  errors.discountPrice}
              </div>
            </div>
            <div className="from-group text-left">
              <h4 className="required-field">Name</h4>
              <input
                type="text"
                className="form-control cal-input"
                placeholder=""
                name="name"
                value={values.name}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.name && touched.name && errors.name}
              </div>
            </div>
            <div className="from-group text-left">
              <h4 className="required-field">Description</h4>
              <textarea
                className="form-control cal-input"
                style={{ height: '150px' }}
                placeholder=""
                name="description"
                value={values.description}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.description &&
                  touched.description &&
                  errors.description}
              </div>
            </div>
            <div className="from-group text-left">
              <h4 className="required-field">Image:</h4>
              <input
                type="file"
                id="imgInp"
                accept="image/*"
                onChange={e => getImage(e)}
              />
              {photoURL && (
                <img className="photo-preview" alt="dishImage" src={photoURL} />
              )}
            </div>
            <div className="error-message">{errorMsgAddDish}</div>
            <div className="text-center">
              <button
                type="submit"
                className="btn btn-primary  m-l-sm"
                disabled={isDisable}
              >
                {isAdd ? 'Add' : 'Update'}
              </button>
              <button
                type="button"
                disabled={isDisable}
                className="btn btn-primary m-l-sm"
                data-dismiss="modal"
                onClick={closeModel}
              >
                Cancel
              </button>
            </div>
          </form>
        )}
      </Formik>
    </Model>
  );
};

export default SetTokenHeader(AddMenuItemsModal, axios);
