/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import AddMenuItemsModal from './AddMenuItemsModal';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import { Table } from '../../../components';
import {
  getAllDishSaga,
  editDishPublishStatusSaga,
  getAllCategoryChefSaga,
  getAllSubCategoryChefSaga,
  deleteDishSaga,
  editProfileSubmitSaga,
} from '../../../store/actions';
import DeleteModel from '../../../components/UI/Model/DeleteModel';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';

// eslint-disable-next-line no-unused-vars
const MenuItems = props => {
  const { isProfilePage, setPageName } = props;
  const [isDisable, setIsDisable] = useState(false);
  const [errorMenuItem, setErrorMenuItem] = useState(false);
  const [mounted, setMounted] = useState(false);
  const [modelOpen, setModelOpen] = useState(false);
  const [modelDeleteOpen, setModelDeleteOpen] = useState(false);
  const [deleteId, setDeleteId] = useState('');
  const [isAdd, setIsAdd] = useState(false);
  const [editData, setEditData] = useState('');
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const [tableFilter, setTableFilter] = useState({});
  const setTableFilters = (columnName, orderBy, search, skip, limit) => {
    tableFilter.columnName = columnName;
    tableFilter.orderBy = orderBy;
    tableFilter.search = search;
    tableFilter.skip = skip;
    tableFilter.limit = limit;
    setTableFilter(tableFilter);
  };

  const dispatch = useDispatch();

  const {
    dishes: { list: allData, totalRecords },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.dish);
  const { open } = useSelector(state => state.modal);

  const getData = (columnName, orderBy, search, skip, limit) => {
    setTableFilters(columnName, orderBy, search, skip, limit);
    if (search && skip !== 0) {
      dispatch(getAllDishSaga({ columnName, orderBy, search, skip: 0, limit }));
    } else {
      dispatch(getAllDishSaga({ columnName, orderBy, search, skip, limit }));
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('name', 'asc', '', 0, 10);
    dispatch(getAllCategoryChefSaga({ forDD: true }));
    dispatch(getAllSubCategoryChefSaga({ forDD: true }));
    return () => setMounted(false);
  }, []);

  const statusAction = data => {
    dispatch(editDishPublishStatusSaga({ id: data.id, status: data.status }));
  };

  const deleteAction = payload => {
    setModelDeleteOpen(true);
    setDeleteId(payload._id);
  };

  const editAction = value => {
    setEditData(value);
    setModelOpen(true);
    setIsAdd(false);
  };

  const tableFields = [
    {
      displayName: 'Image',
      key: 'thumbnail',
      // baseURL: `${process.env.REACT_APP_END_POINT_IMG_URL}/dish/`,
      isMedia: true,
      mediaType: 'image',
      noSort: true,
    },
    {
      displayName: 'Name',
      key: 'name',
    },
    {
      displayName: 'Parent Category',
      key: 'categoryName',
    },
    {
      displayName: 'Sub Category',
      key: 'subCategoryName',
    },
    // {
    //   displayName: 'Food Type',
    //   key: 'isVeg',
    //   isConditional: true,
    //   positiveValue: 'Veg',
    //   negativeValue: 'Non-Veg',
    // },
    {
      displayName: 'Price (₹)',
      key: 'price',
      positiveValue: true,
    },
    {
      displayName: 'Discounted price (₹)',
      key: 'discountPrice',
      positiveValue: true,
    },
    {
      displayName: 'Status',
      key: 'isPublished',
      noSort: true,
      isStatus: true,
      positiveValue: true,
      action: statusAction,
    },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-pencil" aria-hidden="true" />,
          action: editAction,
        },
        {
          view: <i className="fa fa-trash" aria-hidden="true" />,
          action: deleteAction,
        },
      ],
    },
  ];

  const confirmDeleteAction = () => {
    const id = deleteId;
    setDeleteId('');
    dispatch(deleteDishSaga({ id, tableFilter, setModelDeleteOpen }));
  };

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div
      className="wrapper wrapper-content"
      style={isProfilePage ? { padding: 0 } : {}}
    >
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Menu Items </h2>
              </div>
              <div className="text-right  d-flex-right">
                <button
                  type="submit"
                  className="btn btn-primary m-l-sm m-b-md"
                  data-toggle="modal"
                  data-target="#add-items"
                  onClick={() => {
                    setModelOpen(true);
                    setIsAdd(true);
                    setEditData('');
                  }}
                >
                  Add New Items
                </button>
              </div>
              <div className="coman-table" id="owner-detail-btn">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  linkPropsName="_id"
                  setSearchFlag={searchFlagSetter}
                  errorMsg={errorMsg || 'Loading...'}
                />
              </div>
              {isProfilePage && (
                <>
                  {errorMenuItem && allData.length === 0 && (
                    <div className="row">
                      <div className="col-12 error-message">
                        Please add menu items before submit profile
                      </div>
                    </div>
                  )}
                  <div className=" text-center">
                    <button
                      type="button"
                      className="btn btn-primary m-5"
                      disabled={isDisable}
                      onClick={() => setPageName('Order')}
                    >
                      Previous
                    </button>
                    <button
                      type="button"
                      className="btn btn-primary m-5"
                      disabled={isDisable}
                      onClick={() => {
                        if (allData.length === 0) {
                          setErrorMenuItem(true);
                        } else {
                          setIsDisable(true);
                          dispatch(editProfileSubmitSaga({ setIsDisable }));
                        }
                      }}
                    >
                      Submit
                    </button>
                  </div>
                </>
              )}
            </div>
          </div>
        </div>
      </div>
      {modelOpen && (
        <AddMenuItemsModal
          setModelOpen={setModelOpen}
          isAdd={isAdd}
          editData={editData}
          tableFilter={tableFilter}
        />
      )}
      {modelDeleteOpen && (
        <DeleteModel
          setModelOpen={setModelDeleteOpen}
          submitBtnText="Yes"
          submitBtnHandler={confirmDeleteAction}
          cancelBtnText="No"
          closeModel={setModelDeleteOpen}
        />
      )}
      {open && <AlertMessageModal />}
    </div>
  );
};

export default SetTokenHeader(MenuItems, axios);
